import template from './sw-flow-change-shipping-modal.html.twig';

const {Data: {EntityCollection}} = Shopware;
const {Component, Context}       = Shopware;

Component.register('sw-flow-change-shipping-modal', {
    template,

    inject: [
        'repositoryFactory',
    ],

    props: {
        sequence: {
            type:     Object,
            required: true,
        },
    },

    data() {
        return {
            shippingMethod: [],
            amount:         1.0,
            selectedCondition: '',
            conditions:     [
                {
                    'key':   '=',
                    'label': this.$tc('change-shipping-action.conditions.equals'),
                },
                {
                    'key':   '>',
                    'label': this.$tc('change-shipping-action.conditions.greaterThan'),
                },
                {
                    'key':   '>=',
                    'label': this.$tc('change-shipping-action.conditions.greaterThanOrEquals'),
                },
                {
                    'key':   '<',
                    'label': this.$tc('change-shipping-action.conditions.lessThan'),
                },
                {
                    'key':   '<=',
                    'label': this.$tc('change-shipping-action.conditions.lessThanOrEquals'),
                }
            ]
        };
    },

    computed: {
        shippingRepository() {
            return this.repositoryFactory.create('shipping_method');
        },

        shippingMethodId() {
            if (!this.shippingMethod.length) {
                return null;
            }

            return this.shippingMethod[0].id;
        },

        weightAmount() {
            return this.amount;
        },

        conditionOptions() {
            return this.conditions;
        },

        selectedCondition() {
            return this.selectedCondition;
        }
    },

    created() {
        const {config} = this.sequence;

        if (config?.shippingMethod.length > 0) {
            if (config.shippingMethod) {
                this.shippingMethod = config.shippingMethod;
            }
        } else {
            this.shippingMethod = this.createShippingCollection();
        }

        if (config?.weightAmount) {
            this.amount = config.weightAmount;
        }

        if (config?.condition) {
            this.selectedCondition = config.condition;
        }
    },

    methods: {

        createShippingCollection() {
            return new EntityCollection(
                this.shippingRepository.route,
                this.shippingRepository.entityName,
                Context.api,
            );
        },

        onChangeShippingMethod(id, item) {
            const collection = this.createShippingCollection()

            if (item) {
                collection.add(item);
            }

            this.shippingMethod = collection;
        },

        onConditionChange(condition) {
            this.selectedCondition = condition;
        },

        onChangedWeightAmount(amount) {
            this.amount = amount;
        },

        onAddAction() {
            const sequence = {
                ...this.sequence,
                config: {
                    ...this.sequence.config,
                    shippingMethod: this.shippingMethod,
                    weightAmount:   this.amount,
                    condition:      this.selectedCondition,
                },
            };

            this.$nextTick(() => {
                this.$emit('process-finish', sequence);
            });
        },

        onClose() {
            this.$emit('modal-close');
        },

    }
});